import * as React from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';

import {HeaderFont, BodyFont} from './fonts';
import results from '../static/results.png';

export default function Results(props) {
    return (
        <div>
            <HeaderFont>Results</HeaderFont>
            <BodyFont>
                ObjectNav performance for agents trained with different dataset scales. The plots show zero-shot and fine-tuned
                performance on the HM3DSem val set. For the zero-shot agents, we see that agents trained on HSSD-60 and HSSD-122 match
                or outperform agents trained on ProcTHOR-60 and ProcTHOR-122. Moreover, the much larger ProcTHOR-10K dataset does
                not lead to significantly improvements in ObjectNav generalization performance. Surprisingly, fine-tuned agents do not benefit
                from dataset size scaling, with performance even degrading when trained on ProcTHOR-10K.
            </BodyFont>
            <Box sx={{
                display: 'flex',
                justifyContent: 'center',
                my: 5
            }}>
                <Box
                    component="img"
                    sx={{
                        display: 'flex',
                        width: '80%',
                        justifyContent: 'center',
                        my: 5
                    }}
                    alt="results"
                    src={results}
                />
            </Box>
        </div>
    );
}