import * as React from 'react';
import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

function HeaderFont(props) {
    const { sx, ...other } = props;
    return (
        <Box
            sx={{
                bgcolor: (theme) => (theme.palette.mode === 'dark' ? '#101010' : '#fff'),
                color: (theme) => (theme.palette.mode === 'dark' ? "primary.light" : "primary.main"),
                border: 'none',
                p: 0,
                my: 4,
                borderRadius: 2,
                fontSize: '1.7rem',
                fontWeight: 'bold',
                ...sx,
            }}
            {...other}
        />
    );
}

HeaderFont.propTypes = {
    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.oneOfType([
        PropTypes.arrayOf(
            PropTypes.oneOfType([PropTypes.func, PropTypes.object, PropTypes.bool]),
        ),
        PropTypes.func,
        PropTypes.object,
    ]),
};

function BodyFont(props) {
    const { sx, ...other } = props;
    return (
        <Typography
            align="justify"
            sx={{ typography: { xs: 'body', md: 'body', 'lg': 'h6'}, ...sx, }}
            {...other}
        />
    );
}

BodyFont.propTypes = {
    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.oneOfType([
        PropTypes.arrayOf(
            PropTypes.oneOfType([PropTypes.func, PropTypes.object, PropTypes.bool]),
        ),
        PropTypes.func,
        PropTypes.object,
    ]),
};

<Typography align="justify" sx={{ typography: { xs: 'body', md: 'body', 'lg': 'h6' } }}></Typography>

export {HeaderFont, BodyFont};