import * as React from 'react';
import Box from '@mui/material/Box';
import PropTypes from 'prop-types';
import Typography from '@mui/material/Typography';
import Avatar from '@mui/material/Avatar';
import Link from '@mui/material/Link';
import { useMediaQuery, useTheme } from "@mui/material";

import { authors } from '../static/authors';
import georgia_logo from '../static/georgia-tech-logo.svg';
import sfu_logo from '../static/sfu-logo.svg';
import stanford_logo from '../static/stanford-cardinal-logo.svg';
import meta_logo from '../static/meta-logo.svg';

import { HeaderFont, BodyFont } from './fonts';

function LogoBox(props) {
    const { sx, ...other } = props;
    return (
        <Box
            sx={{
                my: 2,
                mx: '5%',
                display: 'flex',
                height: { xs: 30, sm: 40, lg: 60 },
                justifyContent: 'center',
                ...sx,
            }}
            {...other}
        />
    );
}

LogoBox.propTypes = {
    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.oneOfType([
        PropTypes.arrayOf(
            PropTypes.oneOfType([PropTypes.func, PropTypes.object, PropTypes.bool]),
        ),
        PropTypes.func,
        PropTypes.object,
    ]),
};

function People() {
    const theme = useTheme();
    const mq_xs = useMediaQuery(theme.breakpoints.only('xs'));
    const mq_sm = useMediaQuery(theme.breakpoints.only('sm'));
    const mq_md = useMediaQuery(theme.breakpoints.only('md'));
    const mq_lg = useMediaQuery(theme.breakpoints.up('lg'));

    const getNumCols = () => {
        if (mq_xs)
            return 2;
        if (mq_sm)
            return 4;
        if (mq_md)
            return 4;
        if (mq_lg)
            return 6;
    }

    const num_cols = getNumCols();
    const num_rows = Math.ceil(authors.length / num_cols);
    const rows = [...Array(num_rows).keys()];
    return (
        <Box
        >
            {
                rows.map((row_index) => {
                    const cols = [...Array(Math.min(num_cols, authors.length - row_index * num_cols)).keys()];
                    return (
                        <Box
                            sx={{
                                display: 'flex',
                                justifyContent: 'center',
                            }}
                        >
                            {
                                cols.map((col_index) => {
                                    let author = authors[row_index * num_cols + col_index];
                                    return (
                                        <Box sx={{
                                            display: 'block',
                                            justifyContent: 'center',
                                        }}>
                                            <Avatar
                                                alt={author.name}
                                                src={author.photo}
                                                sx={{
                                                    my: 3,
                                                    mx: 2,
                                                    width: 120,
                                                    height: 120,
                                                }}
                                            />
                                            <Box sx={{
                                                display: 'flex',
                                                justifyContent: 'center',
                                            }}>
                                                <Box sx={{ width: 120}} >
                                                    <BodyFont align="center" >{author.name}</BodyFont>
                                                </Box>
                                            </Box>
                                        </Box>
                                    );
                                })
                            }</Box>
                    );
                }
                )
            }
        </Box>
    );
}

export default function Team(props) {
    return (
        <div>
            <HeaderFont>Team</HeaderFont>
            <People />
            <Box
                sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    my: 3,
                }}
            >
                <LogoBox
                    component="img"
                    alt="georgia_logo"
                    src={georgia_logo}
                />
                <LogoBox
                    component="img"
                    alt="sfu_logo"
                    src={sfu_logo}
                />
                <LogoBox
                    component="img"
                    alt="stanford_logo"
                    src={stanford_logo}
                />
                <LogoBox
                    component="img"
                    sx={{
                        height: { xs: 15, sm: 20, lg: 30 },
                    }}
                    alt="meta_logo"
                    src={meta_logo}
                />
            </Box>
        </div>
    );
}